import './waifu.css'

// Recommended to use absolute path for live2d_path parameter
// live2d_path 
const live2d_path = 'https://fastly.jsdelivr.net/npm/live2d-widgets@1.0.0-rc.7/dist/'
// const live2d_path = '/dist/';

// Method to encapsulate asynchronous resource loading
// 
function loadExternalResource(url, type) {
  return new Promise((resolve, reject) => {
    let tag

    if (type === 'css') {
      tag = document.createElement('link')
      tag.rel = 'stylesheet'
      tag.href = url
    } else if (type === 'js') {
      tag = document.createElement('script')
      tag.type = 'module'
      tag.src = url
    }
    if (tag) {
      tag.onload = () => resolve(url)
      tag.onerror = () => reject(url)
      document.head.appendChild(tag)
    }
  })
}

;(async () => {
  // If you are concerned about display issues on mobile devices, you can use screen.width to determine whether to load
  // 
  // if (screen.width < 768) return;

  // Avoid cross-origin issues with image resources
  // 
  const OriginalImage = window.Image
  window.Image = function (...args) {
    const img = new OriginalImage(...args)
    img.crossOrigin = 'anonymous'
    return img
  }
  window.Image.prototype = OriginalImage.prototype
  // Load waifu.css and waifu-tips.js
  //  waifu.css  waifu-tips.js
  await Promise.all([
    // loadExternalResource(live2d_path + 'waifu.css', 'css'),
    loadExternalResource(live2d_path + 'waifu-tips.js', 'js'),
  ])
  // For detailed usage of configuration options, see README.en.md
  //  README.md
  // eslint-disable-next-line no-undef
  initWidget({
    // waifuPath: live2d_path + 'waifu-tips.json',
    waifuPath: '/static/live2d/waifu-tips.json',
    // cdnPath: 'https://fastly.jsdelivr.net/gh/fghrsh/live2d_api/',
    cubism2Path: live2d_path + 'live2d.min.js',
    cubism5Path: 'https://cubism.live2d.com/sdk-web/cubismcore/live2dcubismcore.min.js',
    // tools: ['hitokoto', 'asteroids', 'switch-model', 'switch-texture', 'photo', 'info', 'quit'],
    tools: [],
    logLevel: 'trace',
    drag: false,
  })
})()

